import React from "react";
import { NavLink } from "react-router-dom";
export default function Common(props) {
  return (
    <>
      <section id="header" className="d-flex align-items-center">
        <div className="container-fuild nav_bg">
          <div className="row">
            <div className="col-10 mx-auto">
              <div className="row">
                <div className="col-md-6 pt-5 pt-lg-0 order-2 order-lg-1 d-flex justify-content-center flex-column">
                  <h1>
                    {props.name}
                    <strong className="brand-name"> Code Making</strong>
                  </h1>
                  <h2 className="my-3">
                    We Are Team Of Telented Developer Making Websites
                  </h2>
                  
                  <div className="mt-3">
                    <NavLink className="btn-get-started" to={props.visit}>
                      {props.btname}
                    </NavLink>
                  </div>
                </div>
                <div
                  className="col-lg-6 order-1 order-lg-2 header-img"
                 
                  
                >
                  <img
                    src={props.imgsrc}
                    className="img-fluid animated"
                    alt="hearder_img"
                    
                  ></img>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
}
